export enum PosterVariant {
  CLASSIC = "classic",
  MODERN = "modern",
  FESTIVE = "festive",
  FORMAL = "formal",
  SCENIC = "scenic",
  LANDSCAPE = "landscape",
  MINIMAL = "minimal",
  BADGE = "badge",
  EDITORIAL = "editorial",
  VINTAGE = "vintage",
  ROYAL = "royal",
}

export type FontFamily =
  | "Plus Jakarta Sans"
  | "Cinzel"
  | "Newsreader"
  | "Noto Sans";

export interface PosterData {
  scouterName: string;
  designation: string;
  organization: string;
  quote: string;
  photoUrl: string;
  logoUrl: string;
  accentColor: string;
  secondaryColor: string;
  theme: "light" | "dark";
  variant: PosterVariant;
  fontFamily: FontFamily;
  mainIcon: string;
  photoScale: number;
  photoX: number;
  photoY: number;
  logoScale: number;
}

